import { Injectable } from '@angular/core';

export class GdaStyleTooltip {
  public backgroundColor: string;
  public color: string;
  public padding: string;
  public fontSize: string;
  public borderRadius: string;
  public zIndex: string;

  constructor(
    backgroundColor: string = '#999',
    color: string = '#fff',
    padding: string = '4px 12px',
    fontSize: string = '.8rem',
    borderRadius: string = '5px',
    zIndex: string = '10000'
  ) {
    this.backgroundColor = backgroundColor;
    this.color = color;
    this.padding = padding;
    this.fontSize = fontSize;
    this.borderRadius = borderRadius;
    this.zIndex = zIndex;
  }
}

@Injectable({
  providedIn: 'root'
})
export class GdaTooltipService {
  /**
   * Style
   */
  public styleTooltip: GdaStyleTooltip;

  constructor() {
    this.styleTooltip = new GdaStyleTooltip();
  }
}
